


















































































































@use 'src/styles/responsive' as r;
@use 'src/styles/color';
@use 'src/styles/shape';
@use 'src/styles/mixins';

$margin: 2.5rem;

.drawer {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 16rem;
    max-width: 100%;
    height: 100%;
    z-index: 100;
    transition: width 300ms ease-in-out;
    padding: 1rem 1rem $margin 0;

    .actions {
        display: flex;
        align-items: center;
        justify-content: space-between;
        align-self: stretch;
        margin: 0 0 calc(1rem + 2px) $margin;
        height: 2.25rem;

        .logo {
            svg {
                fill: none;
                stroke: color.$on-surface;
            }

            &:focus:not(:active) {
                outline: none;

                svg {
                    fill: color.$focus;
                    stroke: color.$focus;
                }
            }

            &:hover {
                svg {
                    fill: color.$on-surface;
                }
            }
        }
    }

    .tabs {
        align-self: stretch;
        margin-bottom: auto;

        li {
            width: 100%;

            &:not(:last-child) {
                margin-bottom: 0.25rem;
            }

            a {
                display: flex;
                border-radius: 0 shape.$radius shape.$radius 0;
                padding-left: $margin;
                height: $margin;
                align-items: center;

                @include mixins.outline-interactive;

                &:hover,
                &:focus {
                    background-color: color.$hover;
                }

                &[selected] {
                    background-color: color.$primary;
                    color: color.$on-primary;
                }
            }
        }
    }

    .options {
        margin: 0 0 $margin $margin;
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        > *:not(:last-child) {
            margin-bottom: 1rem;
        }
    }

    .socials {
        margin-left: $margin;
    }

    @include r.lt-md {
        position: absolute;
        border-right: shape.$border;
    }

    @include r.lt-lg {
        &[collapse] {
            left: -16rem;
        }

        @include r.md {
            overflow-x: hidden;
            border: none;

            &[collapse] {
                padding-right: 0;
                width: 0;
            }
        }
    }

    @include r.md {
        padding-right: 0.5rem;
    }

    // Hide Menu button on >= large devices
    @include r.lg {
        .actions .close {
            display: none;
        }
    }
}
