
































@use 'src/styles/mixins';

.picture {
    @include mixins.outline-interactive;

    display: block;
    padding: 0;
    border: none;
    outline: none;
    background-color: transparent;
    position: relative;
    overflow: hidden;

    .loading {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    img {
        height: 100%;
        width: 100%;
        display: block;
        object-fit: cover;
        object-position: center;
    }
}

// transitions
.fade-enter-active,
.fade-leave-active {
    transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
    opacity: 0;
}
